import React from 'react'
import './CardStyle.css';

const Card = (props) => {
    return (
      
        <div className='container-cards'>
            <div className='card'>
                <div className='card-img'>
                    <img id='Img' src={props.imgSrc} alt={props.name} />
                </div>
                <h3>{props.name}</h3>
                <p>{props.title}</p>
                <button id='btn' type='button'><a href={props.link} target='__blank'>Watch Now</a></button>
            </div>
        </div>

    )
}

export default Card